<template>
  <div id='comingsoon'>
    <Loading v-if="loading"></Loading>
    <ul v-else>
      <li v-for="item in view_item" :key=item.aid class="video_item">
        <div class="pic_show" @tap="handleToDetail(item.id)">
          <div class="pic_img">
            <a :href="item.short_link">
              <img :src="item.pic">
              <p class="iconfont icon-bofang">&nbsp;{{item.duration|time}}</p>
            </a>
          </div>
          <div class="info_list">
            <span v-text="item.stat.view" class="iconfont icon-bofang"></span>
            <span v-text="item.stat.reply" class="iconfont icon-pinglun"></span>
            <span v-text="item.stat.favorite" class="iconfont icon-xiai"></span>
          </div>
          <div class="info_tt" v-text="item.title">
          </div>
        </div>
      </li>
    </ul>
    <div class="bottom" v-if="loading_show">
      <div></div>
      <div></div>
      <div></div>
    </div>
  </div>
</template>

<script>
import Loading from '@/components/Loading'
export default {
  name: 'nowplaying',
  components: {
    Loading
  },
  data () {
    return {
      view_item: [],
      loading: true,
      id: 1,
      loading_show: false
    }
  },
  created () {
    this.$axios.get('/x/web-interface/dynamic/region?rid=231&ps=20&pn=' + this.id)
      .then(
        msg => {
          if (msg.status === 200) {
            this.view_item.push(...msg.data.data.archives)
            this.loading = false
            this.id += 1
          } else {
            console('请求出错:', msg.code)
          }
        }
      )
      .catch(() => { })
  },
  mounted () {
    const contain = document.getElementById('comingsoon')
    this.myListen(contain, 'scroll', () => {
      if (contain.clientHeight + Math.ceil(contain.scrollTop) >= contain.scrollHeight) {
        this.loading_show = true
        this.$axios.get('/x/web-interface/dynamic/region?rid=231&ps=20&pn=' + this.id)
          .then(
            msg => {
              if (msg.status === 200) {
                this.view_item.push(...msg.data.data.archives)
                this.loading = false
                this.id += 1
                this.loading_show = false
              } else {
                console('请求出错:', msg.code)
              }
            }
          )
      }
    })
  },
  filters: {
    time (key) {
      return String(parseInt(key / 60) + ':' + parseInt(key % 60))
    }
  }
}
</script>

<style scoped>
  #comingsoon{
    height: 100%;
    overflow: auto;
  }
  ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    background-color: #eff3f4;
  }
  .video_item{
    width: 180px;
    padding: 5px;
    margin: 3px;
    box-sizing: border-box;
    border-radius: 3px;
    background-color: #fdfffe;
    box-shadow: 0px 2px#888;
  }
  .pic_img{
  position: relative;
  }
  .pic_img img{
    width: 170px;
    height: 107px;
  }
  .pic_img p{
    position: absolute;
    bottom: 0px;
    z-index: 4;
    padding: 2px 4px;
    color: white;
    font-size: 10px;
    font-weight: bolder;
  }
  div.info_list{
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 12px !important;
    background-color:#eff3f4;
  }
  .info_list span{
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .info_tt{
    line-height: 20px;
    max-height: 40px;
    text-overflow: ellipsis;
    font-size: 14px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
  }
  .bottom{
    width: 100%;
    height: 20px;
    text-align: center;
  }
  .bottom div{
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 6px;
    background-color: #fff;
    border: 1px solid #888;
    margin: 6px;
  }
  .bottom div:nth-of-type(1){
    animation: slide 1s linear 100ms infinite;
  }
  .bottom div:nth-of-type(2){
    animation: slide 1s linear 400ms infinite;
  }
  .bottom div:nth-of-type(3){
    animation: slide 1s linear 700ms infinite;
  }
  @keyframes slide{
    from{
      transform: scale(1,1);
    }
    50%{
      transform: scale(1.2,1.2);
    }
    100%{
      transform: scale(1,1);
      }
  }
</style>
